.home-page {
    .hero-container {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(3, 1fr);

        // height: 150vh;
        .hero {
            // background-image: url(https://a0.muscache.com/im/pictures/monet/Select-34444025/original/944d56fa-e9a6-48fb-a9c5-e4e3778042d7?im_w=1200);
            // background-image: url(https://adoremaldives.com/wp-content/uploads/2018/04/Vakkaru-Maldives-Over-Water-Pool-Villa_Aerial4.jpg);
            // background-image: url(https://images.unsplash.com/photo-1449844908441-8829872d2607?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2340&q=80);
            // background-image: url(https://images.pexels.com/photos/3051551/pexels-photo-3051551.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2);
            // background-image: url(https://images.pexels.com/photos/8473829/pexels-photo-8473829.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2);
            // background-image: url(https://adoremaldives.com/wp-content/uploads/2018/04/Vakkaru-Over-Water-Villas-Jetty.jpg);
            // background-image: url(https://adoremaldives.com/wp-content/uploads/2020/07/Father-and-Son.jpg);
            background-image: url(https://images.unsplash.com/photo-1512552288940-3a300922a275?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2342&q=80);
    

            background-position-y: -150px;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
            height: 80vh;
            width: 100%;
            grid-row: 1/-1;
            grid-column: 1/-1;

            img {
                width: 100%;
                height: 100%;
                grid-column: 1/-1;
            }


        }

        .hero-text {
            grid-row: 2/3;
            grid-column: 1/-1;
            align-self: center;
            justify-self: center;
            font-size: rem(48px);
            color: #ffffff;
            font-weight: 600;
            // text-shadow: 2px 2px 4px #000000d1;
        }
    }

    // .popular-destinations {
    //     transform: translateY(5vh);
    // }

    & .popular-destinations-title {
        font-size: 28px;
        margin-top: 3rem;
        font-family: manrope-bold;

    }

    .popular-destinations-imgs-container {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-column-gap: 24px;
        margin-top: 2rem;
    }

    .city-container {
        cursor: pointer;
        overflow: hidden;
        position: relative;

        .city-img {
            border-radius: 12px;
            object-fit: cover;
            width: 100%;
            height: 100%;
            aspect-ratio: 1/1;
        }

        span {
            margin-top: 1rem;
            font-size: 1.3em;
            font-family: manrope-medium;

        }
    }

    .become-a-host-container {
        width: 100%;
        margin: 4rem 0;

        .become-a-host-elements-container {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-template-rows: repeat(2, 1fr);
            margin-block-end: 30px;
            justify-items: flex-start;

            .become-a-host-img {
                border-radius: 12px;
                grid-column: 1/-1;
                grid-row: 1/-1;
                max-height: 70vh;
                object-fit: cover;
                width: 100%;
            }

            a {
                grid-column: 1/2;
                grid-row: 1;

                .become-a-host-btn {
                    -webkit-margin-start: 2vw;
                    color: $hoover;
                    font-size: 3.8vw;
                    font-weight: 100;

                    line-height: 1;
                    margin-inline-start: 2vw;
                    margin-top: 1vw;
                    text-shadow: 1px 1px 1px $subtext;
                    height: max-content;
                    border-radius: 12px;
                    padding: 0.1rem;

                    &:hover {
                        text-shadow: 1px 1px 1px $secondary;
                    }
                }
            }
        }
    }

}